.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
}


.footer_img {
    width: 40px;
}

.footer_logo {
    display: flex;
    align-items: center;
    gap: 20px;
    width: 350px;
}

.footer_logo p {
    color: #383838;
    font-size: 46px;
    font-weight: 700;
}

.footer_links {
    display: flex;
    list-style: none;
    gap: 5px;
    color: #252525;
    font-size: 15px;
    width: 600px;
}

.footer_links li {
    cursor: pointer;
}

.footer_social_icons {
    display: flex;
    width: 400px;
}

.footer_social_icons_img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.footer_icons_container {
    padding: 10px;
    padding-bottom: 6px;
}

.footer_copyright {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}

.footer_copyright hr {
    width: 75%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

.footer_copyright p {
    width: 400px;
}