.shop {
  object-fit: contain;
}

.shopTitle {
margin: 75px 0 40px 0;
text-align: center;
font-size: 40px;
}

.products {
height: auto;
display: grid;
align-items: center;
justify-content: center;
grid-template-columns: 1fr 1fr 1fr;
place-items: centre;
}

.product {
  border-radius: 15px;
  width: 300px;
  height: 350px;
  margin: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product img {
  width: 400px;
}

.product .description {
text-align: center;
}

.product:hover {
transition: 0.3s ease-in;
cursor: pointer; 
}

.addToCartBtn {
  background-color: transparent;
  border: 2px solid rgb(19, 19, 19);
  min-width: 100px;
  padding: 5px 10px;
  border-radius: 15px;
}

.addToCartBtn:hover {
  background-color: rgb(19, 19, 19);
  color: #fff;
  cursor: pointer;
}

/* MEDIA QUERIES AT BREAK POINTS */

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  
  .products {
      height: auto;
      display: grid;
      align-items: center;
      justify-content: center;
      grid-template-columns: 1fr;
      place-items: centre;
  }
  
  .product .description {
